import React from 'react';
import {Link} from 'gatsby';
import MenuItem from '../../model/menuitem';
import Constants from '../../utils/constants';

const menuItems:MenuItem[] = [{ url: '/', name: 'Home', children: []}];

menuItems.push({url: '/rules/', name: 'Rules', children: []});
menuItems.push({url: '#', name: 'C-Shirts', children: [
    {url: '/cshirts/', name: "Collection", children:[]},
    {url: '/premint/', name: "Whitelist raffle", children:[]}
]});
var tournamentsArray:MenuItem[] = [];
var tournaments:MenuItem = {url: '/tournaments/', name: 'Tournaments', children: tournamentsArray};
Constants.TOURNAMENTS.forEach(t => {
    var name = t.name;
    if(t.date) {
        const today:any = new Date();
        const diffDays = Math.ceil(Math.abs(t.date as any - today) / (1000 * 60 * 60 * 24));
        if(diffDays< 30) {
            name = name + " (mint soon)"
        }
    }
    tournaments.children.push({url: t.url, name: name, children:[]});
});
menuItems.push(tournaments); 
menuItems.push({url: '/claim/', name: 'Claim wins', children: []});

const Menu = ()  => {
    return (
        <ul id="menu-primary-menu" className="menu">
            {menuItems.map((item: any) => 
                <li className={item.children.length > 0 ? "menu-item menu-item-has-children" : "menu-item"} key={item.name}>
                    <Link to={item.url} activeClassName="current-menu-item">{item.name}</Link>
                    <ul className={item.children.length > 0 ? "sub-menu" : "hide"}>
                        {item.children.map((subitem: any) => 
                            <li className="menu-item" key={subitem.name}>
                                <Link to={subitem.url} activeClassName="current-menu-item">{subitem.name}</Link>
                            </li>
                        )}                                               
                    </ul>
                </li>
            )}
            <li className="menu-item">
                <a className="d-block d-md-none active" href={Constants.DISCORD} target="_blank">
                    <span className="effect">JOIN DISCORD</span>
                </a>
            </li> 
        </ul>
    );
};

export default Menu;